@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
.main_container {
  background-color: #5c48d3;
  padding-bottom: 80px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}

.title {
  color: #ffffff;
  font-size: 60px;
  font-weight: lighter;
}

.title + p {
  color: #ffffff;
  justify-content: center;
  font-size: 23px;
  font-weight: lighter;
}

.img {
  width: 144px;
  height: 144px;
  border-width: 12px;
  border-radius: 100px;
}

.container_fouter {
  display: flex;
  flex-direction: column;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #5c48d3;
  justify-content: center;
  align-items: center;
}

.container_fouter > p {
  flex: 1px 1;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: lighter;
  color: #ffffff;
}

.project {
  margin-bottom: 80px;
  margin-top: 80px;
}
.img_project {
  height: 300px;
  width: 600px;
  max-width: 100%;
  position: relative;
  margin: 10px 10px 10px 10px;
}
.title_project {
  font-size: 23px;
  font-weight: lighter;
  color: #636363;
  padding-bottom: 30px;
}

.link_project {
  text-decoration: none;
  border: solid 1px #dcdcdc;
  padding: 14px;
  border-radius: 5px;
  color: #636363;
}

.link_project:hover {
  background-color: grey;
  text-decoration: none;
  color: white;
}

.item {
  margin: 5px 3% 0 3%;
}
.main_container {
  background-color: #5c48d3;
}
.container_sections {
  border: 1px solid transparent;
  border-radius: 15px;
  margin: 0px 10% 0px 10%;
  background-color: #ffffff;
}

.navBar {
  height: 60px;
  background-color: #f7f7f7;
  border: 1px solid transparent;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.liste {
  display: flex;
  flex: 1 1;
  justify-content: center;
  padding: 10px 5px 10px 5px;
  list-style: none;
  margin: 0;
  border: 0;
  font-size: 20;
  vertical-align: baseline;
}
.link {
  color: #636363;
  text-decoration: none;
  font-size: 18px;
  font-weight: 100;
  padding: 10px 20px 10px 20px;
}

.item :hover {
  color: #636363;
  border-radius: 10px;
  background-color: #ffffff;
  text-decoration: none;
}
.article {
  text-align: center;
  margin: 100px 50px 100px 50px;
}

.image {
  height: 180px;
  width: 90%;
  position: relative;
  margin: 60px 10px 60px 10px;
  border-radius: 10px;
}
.containter_grid {
  text-align: justify;
  font-size: 18px;
  font-weight: 60;
  padding: 5px;
  -webkit-column-count: 2;
          column-count: 2;
  margin: 0px 5% 0px 5%;
}

@media (max-width: 720px) {
  .containter_grid {
    -webkit-column-count: 1;
            column-count: 1;
  }
}

@media (max-width: 870px) {
  .navBar {
    display: none;
  }
}

.titles {
  padding-bottom: 10px;
  font-size: 37px;
  font-weight: lighter;
  margin: 150px 20% 50px 20%;
  color: #636363;
  position: relative;
}
.titles::after {
  content: "";
  display: block;
  position: absolute;
  width: 25%;
  padding-top: 20px;
  border-bottom: solid 2px #5c48d3;
  margin-bottom: 20px;
  left: 38%;
}
p {
  color: #636363;
}

.btn-light,
.btn-light {
  color: gray !important;
  margin: 80px 20% 10px 20%;
}

@media (max-width: 720px) {
  .btn-light,
  .btn-light {
    margin: 80px 70% 10px 0%;
  }
}
.message_succes {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);

  width: 100%;
}

body {
  font-family: "Source Sans Pro", sans-serif;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}
button:focus {
  outline: 0;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}

